import 'ol/ol.css';
import Feature from 'ol/Feature';
import Map from 'ol/Map';
import Point from 'ol/geom/Point';
import Polyline from 'ol/format/Polyline';
import View from 'ol/View';
import {OSM, Vector as VectorSource} from 'ol/source';


import {
    Circle as CircleStyle,
    Fill,
    Icon,
    Stroke,
    Style,
} from 'ol/style';
import {Tile as TileLayer, Vector as VectorLayer} from 'ol/layer';
import {getCenter} from "ol/extent";

const attributions =
    '<a href="https://www.openstreetmap.org/copyright" target="_blank">&copy; OpenStreetMap contributors</a>';
const center = [-0, -0];

const mapView = new View({
    center: center,
    zoom: 1,
    minZoom: 1,
    maxZoom: 20,
});


const map = new Map({
    target: document.getElementById('map'),
    view: mapView,
    layers: [
        new TileLayer({
            source: new OSM(),
        }),
    ],
});

window.addEventListener("message", function(e) {
    const polyline = e.data.geo;

    const pl = new Polyline({
        factor: 1e6,
    });

    const route = pl.readGeometry(polyline, {
        dataProjection: 'EPSG:4326',
        featureProjection: 'EPSG:3857',
    });


    const routeFeature = new Feature({
        type: 'route',
        geometry: route,
    });
    const startMarker = new Feature({
        type: 'icon',
        geometry: new Point(route.getFirstCoordinate()),
    });
    const endMarker = new Feature({
        type: 'icon',
        geometry: new Point(route.getLastCoordinate()),
    });
    const position = startMarker.getGeometry().clone();
    const geoMarker = new Feature({
        type: 'geoMarker',
        geometry: position,
    });

    const styles = {
        'route': new Style({
            stroke: new Stroke({
                width: 6,
                color: [237, 212, 0, 0.8],
            }),
        }),
        'icon': new Style({
            image: new Icon({
                anchor: [0.5, 1],
                src: 'data/icon.png',
            }),
        }),
        'geoMarker': new Style({
            image: new CircleStyle({
                radius: 7,
                fill: new Fill({color: 'black'}),
                stroke: new Stroke({
                    color: 'white',
                    width: 2,
                }),
            }),
        }),
    };

    const vectorLayer = new VectorLayer({
        source: new VectorSource({
            features: [routeFeature, geoMarker, startMarker, endMarker],
        }),
        style: function (feature) {
            return styles[feature.get('type')];
        },
    });

    //clean up from the previous route layer
    var toRemove = new Set();
    map.getLayers().getArray().forEach(l => {
        //console.log(f)
        if(l.isRoute) {
            toRemove.add(l)
        }
    })

    toRemove.forEach(l => {
        map.getLayers().remove(l)
    })

    //add a new route layer
    vectorLayer.__proto__.isRoute = true;
    map.addLayer(vectorLayer);


    mapView.fit(routeFeature.getGeometry(), {padding: [100, 100, 100, 100], minResolution: 50})
}, false);

